import { useGoogleLogin } from '@react-oauth/google';
import React from 'react';
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import { Link, useNavigate } from "react-router-dom";
import axiosConfig from '../../base_url/config';


const GoogleLoginComp = () => {
  const navigate = useNavigate();
  const googleLogin = useGoogleLogin({
    onSuccess: async (tokenResponse) => {
      const userInfo = await axios.get(
        'https://www.googleapis.com/oauth2/v3/userinfo',
        { headers: { Authorization: `Bearer ${tokenResponse.access_token}` } },
      );
      googleAuthBackend(tokenResponse?.access_token);
    },
    onError: errorResponse => console.log(errorResponse),
  });

  //for google auth api

  const googleAuthBackend = async (access_token) => {
    let googleData = {
      access_token: access_token,
      provider: 'google',
    }
    const data = await axiosConfig.post("/social/auth", googleData);
    localStorage.setItem("role", data?.data?.data?.role_id);
    localStorage.setItem("userName", data?.data?.data?.name);
    localStorage.setItem("token", data?.data?.token);
    toast.success("login successfully", {
      position: "bottom-right",
      autoClose: 2000,
    });
    setTimeout(() => {
      if (data?.data?.data?.role_id == 4) {
        navigate("/products");
      } else {
        navigate("/dashboard");
      }
    }, 1000);
  }
  return (
    <>
      {/* <button type="button" className="fa fa-google"onClick={googleLogin}>Sign in with Google 🚀</button> */}
      <div className='d-flex justify-content-center py-3'>
        <button className="d-flex bg-primary text-white justify-content-between align-items-center p-0 m-0 border-0 cursor-pointer" style={{ cursor: "pointer" }} onClick={googleLogin}>
          <div className="google-icon-wrapper bg-white p-2">
            <svg
              className='mt-1'
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              width="18px"
              height="18px"
              viewBox="0 0 48 48"
            >
              <g>
                <path
                  fill="#EA4335"
                  d="M24 9.5c3.54 0 6.71 1.22 9.21 3.6l6.85-6.85C35.9 2.38 30.47 0 24 0 14.62 0 6.51 5.38 2.56 13.22l7.98 6.19C12.43 13.72 17.74 9.5 24 9.5z"
                ></path>
                <path
                  fill="#4285F4"
                  d="M46.98 24.55c0-1.57-.15-3.09-.38-4.55H24v9.02h12.94c-.58 2.96-2.26 5.48-4.78 7.18l7.73 6c4.51-4.18 7.09-10.36 7.09-17.65z"
                ></path>
                <path
                  fill="#FBBC05"
                  d="M10.53 28.59c-.48-1.45-.76-2.99-.76-4.59s.27-3.14.76-4.59l-7.98-6.19C.92 16.46 0 20.12 0 24c0 3.88.92 7.54 2.56 10.78l7.97-6.19z"
                ></path>
                <path
                  fill="#34A853"
                  d="M24 48c6.48 0 11.93-2.13 15.89-5.81l-7.73-6c-2.15 1.45-4.92 2.3-8.16 2.3-6.26 0-11.57-4.22-13.47-9.91l-7.98 6.19C6.51 42.62 14.62 48 24 48z"
                ></path>
                <path fill="none" d="M0 0h48v48H0z"></path>
              </g>
            </svg>
          </div>
          <p className="px-2 m-0">Sign in with Google</p>
        </button>
      </div>
    </>
  )
}

export default GoogleLoginComp